import axios from 'axios';
// import authHeader from './auth-header';

const API_URL = 'https://cryptong.herokuapp.com/api/';

class UserService {
  getPublicContent() {
    return axios.get(API_URL + 'all');
  }

  getUserBoard() {
    return axios.get(API_URL + 'profile', { headers: this.authHeader() });
  }

  getModeratorBoard() {
    return axios.get(API_URL + 'mod', { headers: this.authHeader() });
  }

  getAdminBoard() {
    return axios.get(API_URL + 'admin', { headers: this.authHeader() });
  }
  postUpdateProfile(payload) {
    return axios.post(API_URL + 'profile', payload, { header: this.authHeader() }).then(result => {
      console.log(result);
    }).catch(err => {
      console.log(err);
    });
  }
  authHeader() {

    if (localStorage.getItem('access_token') !== null) {
      const token = (JSON.parse(localStorage.getItem('access_token'))).data.access_token;
      const headers = { Authorization: 'Bearer ' + token };
      return headers;
    } else {
      return {};
    }
  }
}

export default new UserService();